*{box-sizing:border-box}body{margin:0;padding:0;font-family:sans-serif}.container{width:600px;margin:10px auto}.comment{padding:35px;margin:25px 0;transition:all 1s;box-shadow:0 0 10px 1px lightgray;border-radius:0 50px}.comment__text{text-align:justify;white-space:normal;margin:0;line-height:1.25em;font-size:1.4em}.comment__user-name{font-style:italic;font-weight:bold;font-size:1.2em}.comment__footer{display:flex;justify-content:space-between;margin-top:35px}
